@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
/** Media Queries **/
.header {
  background-color: #1b2a4e;
  padding: 1rem 0 3rem 0;
}

.contenido-header .barra {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 3rem;
}
.contenido-header .logo a {
  display: inline-block;
}
.contenido-header .logo img {
  width: 300px;
  height: auto;
}
.contenido-header .derecha .navegacion {
  display: flex;
  gap: 2rem;
}
.contenido-header .derecha .navegacion a {
  text-decoration: none;
  color: #f05112;
  font-weight: 600;
  margin-top: 30px;
}
.contenido-header .derecha .navegacion a:hover {
  color: #fff;
}